
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x:hidden;
  overflow-y:hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.general-button {
  cursor: pointer;
  padding: 0.6rem;
}


.general-button:focus {
  outline: solid 1px white;
}

input {
  font-size: 1.2rem;
  margin: 0.4rem 0 0.4rem 0;
}


:root {
  --main-color:#484d51;
  --second-color: black;
}

/*  
:root {
  --mainColor: #0808088e;
  --second-color: white;
}  */
/* :root {
  --mainColor: #c7ac95;
  --second-color: white;
}  */

@-webkit-keyframes show {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
}
.app-router__profile-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100vw;
  height: 100vh;
  align-items: center;
  text-align: center;
}

.app-router__divider {
  flex: 0.5 1;
}

.app-router__profile-wrapper {
  flex: 8 1;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  z-index: 4;
}

.app__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  position: relative;
  margin: 0;
  height: 100vh;
}


.posts-list__container {
  width: 100%;
  flex: 1 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 3;
  position: relative;
}


.posts-list__container-inner {
  flex-direction: column-reverse;
  display: flex;
  position: relative;
  overflow: hidden;
  padding-bottom: 1rem;
  min-height: 100%;
}
.posts-list__container-inner--empty {
  justify-content: center;
}
.posts-list__start-element {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: var(--main-color);
}

.post-list__loader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
}


.post-list__loader-wrapper--empty {
  height: 100%;
}


.post__container {
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: center;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  width: 100%;
}

.post__detailes-container {
  display: flex;
  flex-direction: column;
  justify-content: start;
  font-size: 0.8rem;
}


.post__text-row {
  display: flex;
}

.post__text {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  word-break: break-all;
  color: white;
  border-radius: 1rem;
  padding: 0.6rem;
}

.post__text--own {
  background-color: var(--main-color);
  margin-left: auto;
}

.post__text--else {
  background-color: #f1f0f0;
  color: black;
  margin-right: auto;
}

.post__visible {
  flex: 20 1;
  display: flex;
}

.post__invisible {
  flex: 15 1;
}

.post__invisible-sides {
  flex: 1 1;
}

.post__delete-button {
  display: flex;
  align-items: flex-end;
  padding: 0;
  margin-left: auto;
}

.post__delete-button--hidden {
  visibility: hidden;
}
.post__text-file {
  width: 100%;
  margin: 0.5rem;
  border: solid black 1px;
  border-radius: 1rem;
}
.post__text-and-button {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.post__text-span {
  margin-right: 1rem;
}
.divider {
  height: 1rem;
}
.error-message {
  color: red;
  background-color: white;
  font-size: 0.8rem;
  font-weight: bold;
  border: solid 1px;
  text-align: center;  
}

.loader__container {
  display: flex;
  background-color: var(--main-color);
  color: white;
  align-items: center;
  padding: 0.5rem;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  justify-content: center;
}

.loader__container--withRadius {
  border-radius: 1rem;
}

.loader__container--inverted {
  background-color: white;
  color: var(--main-color)

}

.loader__spinner {
  border: 5px solid white;
  border-top: 5px solid rgba(0,0,0,0);
  border-radius: 50%;
  width: 25px;
  height: 25px;
  -webkit-animation: spin 1.6s linear infinite;
          animation: spin 1.6s linear infinite;
}

.loader__spinner--inverted {
  border: 5px solid var(--main-color);
  border-top: 5px solid rgba(0,0,0,0);
}


@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.uploaded-file__file-container {
  display: flex;
  flex-direction: column;
  /* margin: 0.5rem auto 0 0.5rem; */
}

.uploaded-file__image {
  width: 150px;
  border: solid 1px var(--main-color);
}

.uploaded-file__image--posted {
  cursor: pointer;
}

.uploaded-file__general-file-container {
  color: var(--main-color);
  border: solid 1px var(--main-color);
  border-radius: 1rem;
  padding: 1rem;
}

.uploaded-file__general-file-container--inverted {
  color: white;
  border: solid 1px white;
}

.uploaded-file__video-container {
  position: relative;
  color: white;
}

.uploaded-file__play-pause-button {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.664);
}

.uploaded-file__fullscreen-button {
  position: absolute;
  right: 0;
  bottom: 0;
}

.confirm__container {
  border: solid 1px;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  display: flex;
  flex-direction: column;
  color: white;
  border-radius: 1rem;
}
.confirm__container--modal {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  background-color: var(--main-color);
}

.confirm__buttons-container {
  display: flex;
  justify-content: space-between;
  padding: 0 1rem 1rem 1rem;
}

.confirm__message {
  font-size: 1.5rem;
  padding: 1rem;
}
.new-posts__container {
  width: 100%;
  display: flex;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  border: solid 1px var(--main-color);
  border-left: none;
  align-items: center;
  background-color: white;
  z-index: 4;
}

.new-posts__form {
  width: 100%;
}
.new-posts__text-area {
  padding: 0.5rem;
  color: var(--main-color);
  line-break: auto;
  line-height: 1;
  border: none;
  width: 100%;
  outline: none;
}

.new-posts__text-area--mobile {
  font-size: 1rem;
}


.new-posts__text-area--tablet {
  font-size: 1.2rem;
}

.new-posts__text-area--desktop-largeDesktop {
  font-size: 1.1rem;
}

.new-posts__button {
  flex-shrink: 0;
  color: var(--main-color);
  font-size: 1.2rem;
  border: none;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0)
}

.new-posts__button--disabled {
  opacity: 0;
  cursor: default;
}


.file-uploader__file-upload-button {
  padding: 0;
}
.file-uploader__file-upload-button:focus {
  outline: solid 1px var(--main-color);
}

.file-uploader__drop-files-box {
  display: flex;
  flex: 10 1;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 20rem;
  height: 20rem;
  border: solid var(--main-color);
  border-style: dashed;
  border-radius: 1rem;
}

.file-uploader__drop-files-box-sides {
  flex: 2 1;
  font-size: 1.5rem;
  color: var(--main-color);
}

.file-uploader__attach-icon {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg)
}
.video-background__overlay {
  background-color: white;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 2;
  opacity: 0.3;
}

.video-background__container {
  width: 100%;
}

.video-background__video {
  position: absolute;
  z-index: 1;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  -webkit-animation: show 1.0s linear;
          animation: show 1.0s linear;
}

.profile__container--loggedin {
  position: absolute;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: white;
  -webkit-animation: show 0.5s linear;
          animation: show 0.5s linear;
  z-index: 4;
  padding: 0.5rem;
  margin: 0.5rem 0  0 0.5rem;
  background-color: var(--second-color);
  border-radius: 1rem; 
}

.profile__container--loggedout {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--main-color);
  border: solid white 1px;
  border-radius: 1rem;
  padding: 1rem;
}

.profile__user-icon {
  display: inline;
  padding: 0.2rem;
}
.profile-form__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-form__buttons-container {
  display: flex;
  flex-direction: column;
}

.profile-form__button {
  padding: 0.4rem 0 0.4rem 0;
  text-align: left;
  font-size: 0.9rem;
}
.login-form__container {
  display: flex;
  flex-direction: column;
}

.login-form__header {
  text-align: center;
  font-size: 1.4rem;
}
.login-form__buttons {
  display: flex;
  justify-content: space-between;
}

.signup-form__container {
  display: flex;
  flex-direction: column;
}

.signup-form__header {
  text-align: center;
  font-size: 1.4rem;
}

.signup-form__radios-container {
  margin-bottom: 1rem;
}
.signup-form__radio {
  display: flex;
  align-items: baseline;
}
.singup-form__radio-divider {
  flex: 1 1;
}
.singup-form__radio-label {
  flex: 25 1;
  text-align: left;
}

.signup-form__buttons-container {
  display: flex;
  justify-content: space-between
}
 .users-list__container {
   display: flex;
   height: 100%;
   flex-direction: column;
   text-align: left;
   justify-content: flex-start;
   padding-top: 1rem;
   -webkit-animation: show 0.5s linear;
           animation: show 0.5s linear;
   flex: 1 1;
 }

 .users-list__header {
   font-size: 1.1rem;
   font-weight: bold;
   margin-bottom: 0.5rem;
 }
.user__user-name-container {
  display: flex;
}
.user__user-name-container--current {
  color: var(--second-color)
}
.error-boundary__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: var(--main-color)
}
